* {
  box-sizing: border-box;
}

p {
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2a2a2a;
  background-color: #e0c9cb;
  padding: 10px 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font-size: 1.1rem;
  font-weight: 600;
  line-height: 1;
  font: inherit;
  max-width: 320px;
  border: 0 solid #e2e8f0;
  border-radius: 1.5rem;
  margin-top: 20px;
  margin-bottom: 16px;
  margin-left: 20px;
  padding: 1rem 1.6rem;
  display: flex;
  box-shadow: 0 0 6px rgba(166, 175, 195, .7);
}

#search-box::-ms-input-placeholder {
  color: #676666;
  text-transform: uppercase;
  align-self: center;
  font-size: small;
  font-weight: 400;
}

#search-box::placeholder {
  color: #676666;
  text-transform: uppercase;
  align-self: center;
  font-size: small;
  font-weight: 400;
}

#search-box:focus {
  outline: 1px solid #ccc;
}

.country-info {
  grid-template-columns: 1fr;
  justify-items: stretch;
  gap: 20px;
  display: grid;
}

@media (min-width: 600px) {
  .country-info {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1200px) {
  .country-info {
    grid-template-columns: repeat(3, 1fr);
  }
}

.card {
  width: 520px;
  background: linear-gradient(90deg, #ebf2fa 50%, #fff 100%);
  border-radius: 10px;
  margin: 0;
  padding: 40px;
  transition: box-shadow .2s linear;
  display: flex;
}

.card:hover {
  box-shadow: inset 0 0 10px rgba(186, 126, 126, .9), 5px 5px 15px rgba(186, 126, 126, .9);
}

.card > div > img {
  object-fit: contain;
  width: 200px;
  align-self: center;
  transition: scale .2s linear;
}

.card:hover img {
  scale: 1.04;
}

.card > ul {
  flex-direction: column;
  gap: 10px;
  list-style: none;
  display: flex;
}

.card p {
  color: #4e4e4e;
  text-shadow: 0 2px 3px rgba(255, 255, 255, .5);
  margin-bottom: 5px;
  font-weight: 800;
}

